import React from 'react';

function Box(props) {
    const desc = props.description;
    const listItems = desc.map((d) =>
        <li>{d}</li>
    );
    return (
        <div class="box">
            <h1 class="infoBoxTitle">{props.title}</h1>
            <ul class="infoBoxDescription">
                {listItems}
            </ul>

        </div>
    )
}


export default Box;